/* main.css */

/* Form Container */
.form-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 40px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f9f9f9;
}

/* Form Header */
h2 {
  text-align: left;
  color: #333;
}

/* Form Group */
.form-group {
  margin-bottom: 15px;
  width: 100%; /* Adjust width as needed */
}

/* Form Label */
.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  text-align: left;
}

/* Input Field, Checkbox Field, Select Field */
.input-field,
.checkbox-field,
.select-field {
  width: 100%;
  padding: 8px;
  margin-top: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 14px;
  text-align: left;
}

/* Checkbox Field Label */
.checkbox-field + label {
  margin-left: 5px;
}

/* Submit Button */
.submit-button {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

/* Hover Styles */
.submit-button:hover {
  background-color: #0056b3;
}

/* Checkbox Container */
.checkbox-container {
  margin-left: 20px; /* Adjust the left margin as needed */
}

/* Checkbox Option */
.checkbox-option {
  margin-bottom: 5px; /* Add space between options */
}

.checkbox-option input[type="checkbox"] {
  margin-right: 5px; /* Add space between checkbox and label */
}

.checkbox-option label {
  font-size: 12px; /* Make the font size smaller */
  margin-left: 5px; /* Add some space between checkbox and label */
}

/* Long Answer Field */
.longanswer-field {
  width: 100%; /* Make the textarea full width */
  min-height: 100px; /* Set the minimum height */
  padding: 8px;
  margin-top: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 14px;
  resize: vertical; /* Allow vertical resizing */
}

/* Radio Option Label */
.radio-option label {
  cursor: pointer;
  margin-left: 25px; /* Indent for bullet effect */
  font-size: 12px; /* Smaller font size */
  font-weight: normal; /* Normal font weight */
  display: flex;
  align-items: center;
}
